<script>

import { Bar } from 'vue-chartjs'

export default {
  extends: Bar,
  data () {
    return {
      datacollection: {
        labels: ['مارس', 'ابريل', 'ماي', 'يونيو', 'يوليوز'],
        datasets: [{
          label: 'الحالات المستبعدة',
          backgroundColor: '#d8b365',
          data: [2462, 27956, 150127, 488113, 71250]// REF:65969-matin
        }, {
          label: 'الحالات المؤكدة',
          backgroundColor: '#0000ff',
          data: [617, 3806, 3042, 5068, 1991]// REF: 1681-matin
        }]
      },
      options: {
        datasets: { bar: { categoryPercentage: 0.35, barPercentage: 0.35 } },
        layout: {
          padding: {
            left: 6,
            right: 6,
            top: 4,
            bottom: 6
          }
        },
        tooltips: {
          displayColors: true,
          callbacks: {
            mode: 'x'
          }
        },
        scales: {
          xAxes: [{
            display: true,
            stacked: true,
            scaleLabel: {
              display: false,
              labelString: 'الاشهر'
            },
            gridLines: {
              display: false
            }

          }],
          yAxes: [{
            display: true,
            stacked: true,
            scaleLabel: {
              display: true,
              labelString: 'عدد الحالات المؤكدة او المستبعدة'
            },
            gridLines: {
              display: true
            }
          }]
        },
        legend: {
          display: true
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  mounted () {
    this.renderChart(this.datacollection, this.options)
  }
}
</script>



