<template>
  <div id='template'></div>
</template>

<script>
const d3 = require('d3')
export default {
  mounted: function () {
    // set the dimensions and margins of the graph
    var margin = { top: 20, right: 60, bottom: 30, left: 50 }
    var w = 960 // - margin.left - margin.right
    var h = 500 // - margin.top - margin.bottom
    // parse the date / time
    var parseTime = d3.timeParse('%d-%b-%y')
    // set the ranges
    var x = d3.scaleTime().range([0, w])
    var y = d3.scaleLinear()// .range([h, 0])
      .rangeRound([h, 0])
    //
    var svg = d3.select('#template').append('svg')
      // .attr('width', w + margin.left + margin.right)
      // .attr('height', h + margin.top + margin.bottom)
      .attr('viewBox', [0, 0, (w + margin.left + margin.right), (h + margin.top + margin.bottom)])
      // .attr('preserveAspectRatio', 'xMinYMin meet')
      // .attr('viewBox', '0 0 ' + (w + margin.left + margin.right) + ' ' + (h + margin.top + margin.bottom))
      .append('g')
      .attr('transform', 'translate(' + margin.left + ',' + margin.top + ')')
    // Get the data
    d3.csv('static/data/template.csv', function (error, data) {
      // console.log(data)
      if (error) throw error

      // format the data
      data.forEach(function (d) {
        d.date = parseTime(d.date)
        d.plat = +d.plat
      })
      // Scale the range of the data
      x.domain(d3.extent(data, function (d) { return d.date }))
      y.domain(d3.extent(data, function (d) { return d.plat }))
      // console.log()
      // Add the paths with different curves.
      svg.append('path')
        .datum(data)
        .attr('class', 'line')
        // .style('stroke', 'purple')
        // .attr('id', 'tag' + i) // assign ID
        .attr('d', d3.line()
          .curve(d3.curveCardinal)
          .x(function (d) { return x(d.date) })
          .y(function (d) { return y(d.plat) })
        )
      // Add the Legend
      svg.append('text')
        .attr('x', w - 5) // space legend
        .attr('y', margin.top + 20)
        .attr('class', 'legend') // style the legend
        .style('fill', 'blue')
        .text('daCurve.curveTitle')
      /*
      svg.selectAll('dot')
        .data(data)
        .enter()
        .append('circle')
        .attr('r', 4)
        .attr('cx', function (d) { return x(d.date) })
        .attr('cy', function (d) { return y(d.plat) })
*/

      // Add the X Axis
      svg.append('g')
        .attr('class', 'axis')
        .attr('transform', 'translate(0,' + h + ')')
        // .call(d3.axisBottom(x))
        .call(d3.axisBottom(x).tickSizeOuter(0))

      // Add the Y Axis
      svg.append('g')
        .attr('class', 'axis')
        .call(d3.axisLeft(y).ticks(null, 's'))
        // .attr('transform', 'translate(0,' + margin.left + ')')
        .call(d3.axisLeft(y).tickSizeOuter(0))
    })
  }
}
</script>
<style>
.line {
  fill: none;
  stroke: rgb(245, 5, 5);
  stroke-width: 8px;
}
.legend {
    font-size: 14px;
    font-weight: bold;
    text-anchor: left;
}
</style>



